a {
  display: block;
  text-decoration: none;
}

.user {
  width: 80%;
  margin: 3% auto;
  position: relative;
}

.backBtn {
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  padding: 1% 2%;
  background: #333;
  color: #fff;
  font-size: 1.3vw;
  letter-spacing: 1px;
  border-radius: 5px;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.backBtn:hover {
  -webkit-box-shadow: -1px 1px 10px #333, 1px -1px 10px #333;
          box-shadow: -1px 1px 10px #333, 1px -1px 10px #333;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.detailes {
  width: 50%;
  margin: 0 auto;
  border: 1px solid #ccc;
  padding: 2%;
  font-size: 1.7vw;
  color: #333;
  background: #fefefe;
  -webkit-box-shadow: -1px 1px 10px #ccc, 1px -1px 10px #ccc;
          box-shadow: -1px 1px 10px #ccc, 1px -1px 10px #ccc;
  border-radius: 7px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: column;
      flex-direction: column;
  text-shadow: 1px 1px 3px #000;
}

.image {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  margin-bottom: 5%;
}

.image img {
  width: 250px;
  height: 250px;
  border-radius: 50%;
}
.login {
  width: 50%;
  padding-top: 7%;
}

.login h2 {
  padding: 4% 0;
  letter-spacing: 1.5px;
  font-size: 2.5vw;
}

.login p {
  padding: 0 0 2% 0;
  letter-spacing: 1px;
}

.links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  padding: 2% 0 1%;
}

.follow {
  padding: 0 0 2% 0;
  width: 30%;
  margin: 0 auto;
  color: #4e32f0;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.follow:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.follow span {
  font-weight: bold;
  padding-left: 2%;
  color: #333;
}

.view a {
  background: #333;
  padding: 2% 0;
  width: 35%;
  margin: 2% auto;
  border-radius: 7px;
  color: #fff;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
}

.view a:hover {
  -webkit-box-shadow: -1px 1px 10px #333, 1px -1px 10px #333;
          box-shadow: -1px 1px 10px #333, 1px -1px 10px #333;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

@media (max-width: 1200px) {
  .image img {
    width: 200px;
    height: 200px;
  }
}

@media (max-width: 1000px) {
  .image img {
    width: 160px;
    height: 160px;
  }
}

@media (max-width: 800px) {
  .image img {
    width: 120px;
    height: 120px;
  }
}

@media (max-width: 550px) {
  .image img {
    width: 75px;
    height: 75px;
  }
}



